import * as React from 'react';
import Product from '../models/product';

interface ProductCardInterface {
    img: string,
    descripcion: string,
    precio: string,
    uni_medida: string,
    noParte: string,
    idProd: string,
    currency: string,
    stock?: string,
    prodsSolicitar: Product[],
    setProdsSolicitar: React.Dispatch<React.SetStateAction<Product[]>>
    onClickAgregar?: (id: string) => boolean
}

function ProductCard(p: ProductCardInterface) {
    return (
        <div className="card box" style={{ width: 300 }}>
            <div className="card-image columns is-vcentered is-centered mt-2">
                <img src={p.img} alt="Placeholder" style={{ objectFit: "contain", width: 250, height: 200 }} />
            </div>
            <div className="card-content">
                <div className="content">
                    <h6 className="title is-6" style={{
                        height: 50,
                        overflow: "hidden",
                        whiteSpace: "normal",
                        textOverflow: "ellipsis",
                        wordBreak: "break-word",
                        wordWrap: "break-word"
                    }}>
                        {p.descripcion}
                    </h6>
                    <div className="subtitle is-6">
                        <div>
                            {p.precio !== "" ? `${p.precio} ${p.currency}` : ""}{ }
                        </div>
                        <div>
                            {p.uni_medida}
                        </div>
                    </div>
                    <div className="subtitle is-6 is-italic">
                        {"Disponible: " + p.stock}
                    </div>
                    <span className="tag is-info is-medium">
                        {p.noParte}
                    </span>
                </div>
            </div>
            <footer className="card-footer">
                {
                    !p.prodsSolicitar.some(e => e.id === p.idProd)
                        ? <a
                            href="#!"
                            className="card-footer-item"
                            onClick={() => {
                                let addItem = true;
                                if (p.onClickAgregar !== undefined) {
                                    addItem = p.onClickAgregar(p.idProd);
                                }
                                if (addItem) {
                                    p.setProdsSolicitar(prodsSolicitar => {
                                        return [...prodsSolicitar, {
                                            id: p.idProd,
                                            codigo: p.noParte,
                                            cantidad: "1",
                                            precioU: p.precio,
                                            precioT: p.precio,
                                            numPedido: "0",
                                            folio: "0",
                                            comentarios: p.descripcion,
                                            tipo_equipo: "",
                                            numEconomico: "",
                                            currency: p.currency,
                                            stock: p.stock
                                        }]
                                    });
                                }
                            }}
                        >Agregar</a>
                        : <React.Fragment>
                            <a
                                href="#!"
                                className="card-footer-item" onClick={() => {
                                    const index = p.prodsSolicitar.findIndex(e => e.id === p.idProd);
                                    let cant = parseInt(p.prodsSolicitar[index]?.cantidad as string) - 1;
                                    if (cant < 1) {
                                        p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.filter(f => f.id !== p.idProd))
                                    }
                                    else {
                                        p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.map(f => {
                                            if (f.id === p.idProd) {
                                                return { ...f, cantidad: `${cant}`, precioT: `${cant * parseFloat(f.precioU)}` }
                                            }
                                            else {
                                                return f
                                            }
                                        }))
                                    }

                                }}>-</a>
                            <input className="input card-footer-item has-text-link ml-6 mt-3"
                                pattern="[0-9]*"
                                value={p.prodsSolicitar.find(e => e.id === p.idProd)?.cantidad}
                                onChange={e => {
                                    p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.map(f => {
                                        if (f.id === p.idProd) {
                                            if (parseInt(e.target.value) > parseInt(f.stock as string))
                                                return { ...f, cantidad: `${f.stock}`, precioT: `${parseInt(f.stock as string) * parseFloat(f.precioU)}` }

                                            return { ...f, cantidad: `${e.target.value}`, precioT: `${parseInt(e.target.value) * parseFloat(f.precioU)}` }
                                        }
                                        else {
                                            return f
                                        }
                                    }))
                                }}
                                onBlur={e => {
                                    if (e.currentTarget.value === "") {
                                        p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.filter(f => f.id !== p.idProd))
                                    }
                                    const value = parseInt(e.currentTarget.value);
                                    if (value < 1) {
                                        p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.filter(f => f.id !== p.idProd))
                                    }
                                    else {
                                        p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.map(f => {
                                            if (f.id === p.idProd) {
                                                return { ...f, cantidad: `${value}`, precioT: `${value * parseFloat(f.precioU)}` }
                                            }
                                            else {
                                                return f
                                            }
                                        }))
                                    }
                                }}
                            />
                            <a className="card-footer-item"></a>
                            <a href="#!" className="card-footer-item" onClick={() => {
                                const index = p.prodsSolicitar.findIndex(e => e.id === p.idProd);
                                let cant = parseInt(p.prodsSolicitar[index]?.cantidad as string) + 1;
                                p.setProdsSolicitar(prodsSolicitar => prodsSolicitar.map(f => {
                                    if (f.id === p.idProd) {
                                        if (cant > parseInt(f.stock as string))
                                            return { ...f, cantidad: `${f.stock}`, precioT: `${parseInt(f.stock as string) * parseFloat(f.precioU)}` }

                                        return { ...f, cantidad: `${cant}`, precioT: `${cant * parseInt(f.precioU)}` }
                                    }
                                    else {
                                        return f
                                    }
                                }))
                            }}>+</a>
                        </React.Fragment>
                }
            </footer>
        </div >
    );
}

export { ProductCard };
